:root {
  --padd-xl: 75px;
}

/* 
formats:
.otf = "OpenType"
.ttf = "TrueType"
.woff2 = "woff2"
.woff = "woff"


weights:
100 - Thin
200 - Extra light (Ultra light)
300 - Light
400 - Normal (Regular)
500 - Medium
600 - Semi Bold (Demi bold)
700 - Bold
800 - Extra bold (Ultra bold)
900 - Black (Heavy)

example:
  @font-face {
    font-family: "FontName";
    src: url("..dir/fontname.otf") format("OpenType");
    font-weight: 400;
  }

*/
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-SemiBold.ttf") format("TrueType");
  font-weight: 600;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.ttf") format("TrueType");
  font-weight: 500;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.ttf") format("TrueType");
  font-weight: 400;
}
/*
  example:
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: #fff
  }

  h1 {
    @include font(128, 122, 500);

    @include media-breakpoint-down(md) {
      @include font(56, 53, 500);
    }
  }

  h2 {
    @include font(100, 96, 500);

    @include media-breakpoint-down(md) {
      @include font(44, 42, 500);
    }
  }

  p {
    font-family: "BwMitga";
  }
*/
body {
  position: relative;
  font-family: "Poppins";
}
body.hidden {
  overflow: hidden;
}

:root {
  --cont-sm: 540px;
  --cont-md: 720px;
  --cont-lg: 960px;
  --cont-xl: 1140px;
  --cont-2xl: 1320px;
  --cont-3xl: 1400px;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media (max-width: 1920px) {
  .container-down-3xl {
    max-width: 100%;
    padding: 0;
  }
}
@media (max-width: 1400px) {
  .container-down-2xl {
    max-width: 100%;
    padding: 0;
  }
}
@media (max-width: 1200px) {
  .container-down-xl {
    max-width: 100%;
    padding: 0;
  }
}
@media (max-width: 992px) {
  .container-down-lg {
    max-width: 100%;
    padding: 0;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 32px;
  }
  .container-down-md {
    max-width: 100% !important;
    padding: 0 !important;
  }
}
@media (max-width: 576px) {
  .container {
    padding: 0 35px;
  }
  .container-down-sm {
    max-width: 100%;
    padding: 0;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: var(--cont-sm);
  }
  .flex-sm {
    display: flex;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: var(--cont-md);
  }
  .none-md {
    display: none;
  }
  .block-md {
    display: block;
  }
  .flex-md {
    display: flex;
  }
  .flex-column-md {
    flex-direction: column;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: var(--cont-lg);
  }
  .none-lg {
    display: none;
  }
  .block-lg {
    display: block;
  }
  .flex-lg {
    display: flex;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: var(--cont-xl);
  }
  .block-xl {
    display: block;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: var(--cont-2xl);
  }
  .block-2xl {
    display: block;
  }
}
@media (min-width: 1920px) {
  .container {
    max-width: var(--cont-3xl);
  }
  .block-3xl {
    display: block;
  }
}
.flex {
  display: flex;
}

.flex-reverse {
  flex-direction: row-reverse;
}

.flex-column {
  flex-direction: column;
}

.wrap {
  flex-wrap: wrap;
}

.ai-center {
  align-items: center;
}

.ai-end {
  align-items: flex-end;
}

.ai-start {
  align-items: flex-start;
}

.jc-center {
  justify-content: center;
}

.jc-around {
  justify-content: space-around;
}

.jc-between {
  justify-content: space-between;
}

.jc-evenly {
  justify-content: space-evenly;
}

.aside {
  padding: 36px 28px 76px;
  height: 100%;
  background-color: #fff;
}
.aside__current-page {
  line-height: 24px;
}
@media (max-width: 768px) {
  .aside {
    max-width: 320px;
    position: fixed;
    z-index: 2;
    height: 100%;
    width: 100%;
    transition: ease-in-out 250ms;
    transform: translateX(-100%);
  }
}
.aside.active {
  transform: translateX(0%);
}

.logo {
  margin-bottom: 65px;
}
.logo__title {
  font-size: 26px;
  line-height: 39px;
  font-weight: 600;
  margin-left: 8px;
}
.logo__title span {
  font-size: 10px;
  line-height: 15px;
  font-weight: 500;
  color: #838383;
  margin-left: 4px;
}
@media (max-width: 768px) {
  .logo {
    margin-bottom: 20px;
  }
}

.with-icon {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  color: #9197b3;
  display: block;
  position: relative;
  padding: 13px 0 12px 50px;
  width: 250px;
  border-radius: 8px;
  margin-bottom: 18px;
}
.with-icon::before {
  content: "";
  position: absolute;
  left: 13px;
  width: 24px;
  height: 24px;
  background-image: var(--bg-image);
}
@media (max-width: 768px) {
  .with-icon {
    margin-bottom: 10px;
    padding: 10px 0 10px 50px;
  }
}

.links a::after {
  content: url(../../../images/svg/arrow.svg);
  position: absolute;
  top: calc(50% - 10px);
  right: 15px;
}
.links a.active {
  pointer-events: none;
  background-color: #5932ea;
  color: #fff;
}
.links a.active::after path, .links a.active::before path {
  stroke: #fff;
  fill: #fff;
}

.account {
  position: absolute;
  bottom: 76px;
}
.account__image {
  margin-right: 12px;
}
.account__text p:first-child {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
}
.account__text p:last-child {
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
  color: #757575;
}
@media (max-width: 768px) {
  .account {
    bottom: 25px;
    left: 40px;
  }
}

.main {
  width: 100%;
  height: 100%;
  padding: 41px 95px 250px 71px;
  background-color: #fafbff;
}
.main__title {
  font-size: 24px;
  line-height: 36px;
  font-weight: 500;
  margin-bottom: 51px;
}
@media screen and (max-width: 768px) {
  .main__title {
    text-align: center;
  }
}
.main__footer {
  padding: 32px 40px 40px;
}
@media (max-width: 768px) {
  .main__footer {
    padding: 15px 40px 40px;
  }
  .main__footer p {
    margin-bottom: 15px;
    color: #b5b7c0;
  }
}
.main.active {
  transition: ease-in-out 250ms;
}
.main.active::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(41, 45, 50, 0.431372549);
}
@media screen and (max-width: 1200px) {
  .main {
    padding: 40px 20px;
  }
}
@media screen and (max-width: 576px) {
  .main {
    padding: 40px 0;
  }
}

.frame {
  background-color: #fff;
  border-radius: 30px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
}
@media screen and (max-width: 1100px) {
  .frame {
    font-size: 10px;
    line-height: 21px;
    font-weight: 500;
  }
}

.frame-head {
  padding: 30px 53px 40px 38px;
}
.frame-head__text p:first-child {
  font-size: 22px;
  line-height: 33px;
  font-weight: 600;
  margin-bottom: 7px;
}
.frame-head__text p:last-child {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #16c098;
}
@media (max-width: 768px) {
  .frame-head__text p {
    text-align: center;
  }
  .frame-head__text p:first-child {
    font-size: 14px;
    line-height: 18px;
    font-weight: 600;
  }
  .frame-head__text p:last-child {
    font-size: 12px;
    line-height: 16px;
    font-weight: 600;
    margin-bottom: 15px;
  }
}
.frame-head__search {
  height: 38px;
  padding: 10px 11px 12px 11px;
  background-color: #f9fbff;
  border-radius: 10px;
}
.frame-head__search input {
  border-width: 2px 0;
  border-color: transparent;
  width: calc(100% - 24px);
  font-size: 12px;
  line-height: 22px;
  font-weight: 400;
}
.frame-head__search input::placeholder {
  color: #b5b7c0;
}
.frame-head__search input:focus-visible {
  outline: none;
}

@media (max-width: 768px) {
  .table-wrapper {
    overflow-x: scroll;
  }
}

.table {
  width: 100%;
  border: 0;
  border-collapse: collapse;
}
.table thead {
  position: relative;
  color: #b5b7c0;
}
.table thead::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 1px;
  border-bottom: 1px solid #eeeeee;
}
.table thead th {
  padding-bottom: 14px;
}
.table thead th:first-child {
  padding-left: 38px;
}
.table thead th:last-child {
  padding-right: 38px;
}
.table tbody {
  padding: 0 38px;
  color: #292d32;
}
.table tbody tr {
  position: relative;
}
.table tbody tr::after {
  content: "";
  position: absolute;
  width: calc(100% - 78px);
  left: 41px;
  height: 1px;
  border-bottom: 1px solid #eeeeee;
  bottom: 0;
}
.table tbody td:first-child {
  padding-left: 38px;
}
.table tbody td:last-child {
  padding-right: 38px;
}
.table th,
.table td {
  white-space: nowrap;
  text-align: left;
  padding: 24px 0;
  padding-right: 15px;
}
.table td .status {
  padding: 4px 12px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  background-color: #ffc5c5;
  border: 1px solid #df0404;
  border-radius: 4px;
  color: #df0404;
  text-align: center;
}
@media screen and (max-width: 1100px) {
  .table td .status {
    padding: 4px 6px;
    font-size: 10px;
    line-height: 21px;
    font-weight: 500;
  }
}
.table td.active .status {
  color: #00b087;
  border-color: #00b087;
  background-color: rgba(22, 192, 152, 0.38);
}

.switch-buttons li {
  color: #404b52;
  cursor: pointer;
  width: 26px;
  height: 24px;
  padding: 0 9px;
  border-radius: 4px;
  border: 1px solid #eeeeee;
  background-color: #f5f5f5;
  margin-right: 10px;
}
.switch-buttons li::last-child {
  margin-right: unset;
}
.switch-buttons li p {
  font-size: 12px;
  line-height: 24px;
  font-weight: 500;
}

.page-count {
  position: relative;
}
.page-count.active {
  background-color: #5932ea;
  color: #fff;
  pointer-events: none;
}
.page-count.hidden {
  background-color: transparent;
  border: unset;
  pointer-events: none;
}

.mobile-button {
  display: none;
  width: 30px;
  height: 30px;
}
@media (max-width: 768px) {
  .mobile-button {
    display: block;
    position: absolute;
    top: 30px;
    left: 30px;
  }
}
.mobile-button .line {
  display: block;
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #000;
  margin-bottom: 10px;
}
.mobile-button .line:last-child {
  margin-bottom: unset;
}

.sitemap {
  padding: 40px;
}
.sitemap__count {
  margin-bottom: 20px;
}
.sitemap__items {
  display: flex;
  flex-wrap: wrap;
}
.sitemap__items a {
  margin: 0 20px 20px 0;
  padding: 8px 12px;
  border: 1px solid #000000;
  border-radius: 12px;
}